import React from 'react';

function InputForm({ inputs, onInputChange, onCalculate, onReset }) {
  return (
    <form onSubmit={(e) => e.preventDefault()}>
      <div className="row">
        <div className="col">
          <label htmlFor="centerName">Transplant Center</label>
          <input 
            id="centerName" 
            type="text" 
            placeholder="e.g., St. Mercy Transplant Center" 
            value={inputs.centerName}
            onChange={(e) => onInputChange('centerName', e.target.value)}
          />
        </div>
        <div style={{width: '140px'}}>
          <label htmlFor="centerId">Center ID</label>
          <input 
            id="centerId" 
            type="text" 
            placeholder="e.g., CASF" 
            value={inputs.centerId}
            onChange={(e) => onInputChange('centerId', e.target.value)}
          />
        </div>
      </div>

      <div className="row">
        <div className="col">
          <label htmlFor="numTransplants">Expected Transplants (annual)</label>
          <div style={{ display: 'flex', gap: '1rem', alignItems: 'center' }}>
            <input 
              id="numTransplants" 
              type="range" 
              min="1" 
              max="1000" 
              step="1" 
              value={inputs.numTransplants}
              onChange={(e) => onInputChange('numTransplants', e.target.value)}
              style={{ flex: 1 }}
            />
            <input 
              type="number" 
              min="1" 
              max="1000" 
              step="1" 
              value={inputs.numTransplants}
              onChange={(e) => onInputChange('numTransplants', e.target.value)}
              style={{ width: '100px' }}
            />
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col">
          <label htmlFor="offerAcceptRate">Offer Acceptance Rate (center prior %)</label>
          <input 
            id="offerAcceptRate" 
            type="number" 
            min="0" 
            max="100" 
            step="0.1" 
            value={inputs.offerAcceptRate}
            onChange={(e) => onInputChange('offerAcceptRate', e.target.value)}
          />
        </div>
        <div className="col">
          <label htmlFor="graftSurvival">Graft Survival Rate (1yr %)</label>
          <input 
            id="graftSurvival" 
            type="number" 
            min="0" 
            max="100" 
            step="0.1" 
            value={inputs.graftSurvival}
            onChange={(e) => onInputChange('graftSurvival', e.target.value)}
          />
        </div>
      </div>

      <p className="notes">
        This single-page tool accepts a set of inputs and will POST them to an external IOTA calculation API (or run a local simulation). The API should return per-transplant upside (1yr), downside (2yr), and distribution percentiles for key metrics. Results are displayed as per-transplant payments, totals, percentiles, and charts.
      </p>

      <div style={{ marginTop: '1rem', display: 'flex', gap: '0.5rem' }}>
        <button className="btn primary" onClick={onCalculate}>
          Run Model
        </button>
        <button className="btn" onClick={onReset}>
          Reset
        </button>
      </div>
    </form>
  );
}

export default InputForm;
